<template>
  <div class="row">
    <div v-if="todo" class="col s6 offset-s3">
      <h1>{{todo.title}}</h1>

      <form>
        <ul class="browser-default">
          <li>
            <div class="input-field">
              <input
                id="item"
                type="text"
                title="push Enter to add"
                placeholder="Todo item"
                :class="{'invalid': itemError}"
                v-model="newItem"
                @keyup.enter="addItem"
                maxlength="50"
              >
              <span class="character-counter" style="float: right; font-size: 12px;">{{newItem.length}}/50</span>
              <span class="helper-text">{{itemError}}</span>
            </div>
          </li>
          <li v-for="(item, index) in itemsList" :key="index" style="position: relative">
            <input
              type="text"
              :value="item"
              @blur="changeInput($event.target.value, index)"
              maxlength="50"
            >
            <span class="remove" @click="removeItem(index)">x</span>
          </li>
        </ul>
        <button class="btn" type="button" @click.prevent="updateTodo" :disabled="itemsList.length === 0" style="margin-right: 10px;">Update</button>
        <button class="btn purple lighten-1" type="button" @click.prevent="cancel">Cancel</button>
      </form>
    </div>
    <p v-else>No todo</p>
  </div>
</template>

<script>
export default {
  computed: {
    todo() {
      return this.$store.getters.todoId(+this.$route.params.id)
    }
  },
  data() {
    return {
      itemsList: [],
      newItem: '',
      itemError: '',
    }
  },
  methods: {
    addItem() {
      if (this.item.length === 0) {
        this.itemError = "Empty item";
      } else if (this.itemsList.indexOf(this.item) != -1) {
        this.itemError = "Item is already exist";
      } else  {
        this.itemsList.push(this.item);
        this.itemError = '';
        this.newItem = '';
      }
    },
    changeInput(newItemName, index) {
      if (newItemName.length === 0) {
        this.removeItem(index);
      } else {
        this.itemsList.splice(index, 1, newItemName)
      }
      this.itemsList = Array.from(new Set(this.itemsList))
    },
    removeItem(index) {
      this.itemsList.splice(index, 1);
    },
    updateTodo() {
      this.$store.dispatch('updateTodo', {
        id: this.todo.id,
        itemsList: this.itemsList,
      });
      this.$router.push('/')
    },
    cancel() {
      const arrTodos = JSON.parse(localStorage.getItem('todos'));
      const todo = arrTodos.find(t => t.id === this.todo.id);
      this.itemsList = todo.itemsList;
    }
  },
  created() {
    this.itemsList = this.todo.itemsList;
  }
}
</script>

<style lang="scss" scoped>
.remove {
  display: none;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 14px;
  height: 14px;
  font-size: 12px;
  line-height: 10px;
  text-align: center;
  border-radius: 50%;
  background-color: #ccc;
  cursor: pointer;
}
li:hover > .remove {
  display: block;
}
.helper-text {
  color: #F44336;
}
</style>